import {External} from "@/type"

export function useLyk() {
    const isLykEnabled = import.meta.env.VITE_USE_LYK === "true";

    const devTips = (): boolean => {
        if (!window.external && isLykEnabled) {
            console.error('.env');
            return false;
        }
        return true;
    };

    const isLyk = (): boolean => {
        const userAgent = navigator.userAgent;
        return userAgent.indexOf('Lyk4') > -1;
    }

    const withLyk = <T extends keyof External>(type: T, ...args: any[]) => {
        if (!devTips()) return false;

        if(!isLyk()){
            alert("")
        }

        if (typeof window !== "undefined") {
            // @ts-ignore
            const callMethod = window.external?.[type];
            if (callMethod) {
                try {
                    console.log('', type,'===','', ...args);
                    // @ts-ignore
                    return callMethod(...args);
                } catch (error) {
                    console.error("", type, "", error);
                }
            } else {
                console.error('', type, '===', '', ...args);
            }
        } else {
            console.error("windows");
        }
        return undefined;
    };

    return { withLyk };
}
